import { mapHeaders } from "../utils/mapHeaders";
import { toJsonMapCollection } from "../utils/toJsonMapCollection";
import { JsonMap } from "./JsonMap";
export class JsonMedia extends JsonMap {
    groups = [];
    groupsName;
    allowedGroups;
    schema(schema) {
        this.set("schema", schema);
        return this;
    }
    examples(examples) {
        this.set("examples", examples);
        return this;
    }
    toJSON(options = {}) {
        let groups = [...(this.groups || [])];
        return super.toJSON({ ...options, groups, groupsName: this.groupsName });
    }
}
export class JsonResponse extends JsonMap {
    status;
    constructor(obj = {}) {
        super(obj);
        this.content(obj.content || {});
    }
    description(description) {
        this.set("description", description);
        return this;
    }
    headers(headers) {
        this.set("headers", mapHeaders(headers));
        return this;
    }
    content(content) {
        this.set("content", toJsonMapCollection(content, JsonMedia));
        return this;
    }
    getContent() {
        return this.get("content");
    }
    getMedia(mediaType, create = true) {
        create && this.addMedia(mediaType);
        return this.getContent()?.get(mediaType);
    }
    addMedia(mediaType) {
        const content = this.get("content");
        if (!content.has(mediaType)) {
            content.set(mediaType, new JsonMedia());
        }
        return this;
    }
    toJSON(options = {}) {
        const response = super.toJSON(options);
        if (this.status === 204) {
            delete response.content;
        }
        if (response.headers) {
            Object.entries(response.headers).forEach(([key, { type, ...props }]) => {
                response.headers[key] = {
                    ...props,
                    schema: {
                        type
                    }
                };
            });
        }
        return response;
    }
}
